import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom'


export default function Navbar(props) {

    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.color} bg-${props.color}`}>
            <div className="container-fluid">
                <a className="navbar-brand" href="/">{props.title}</a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <a className="nav-link active" aria-current="page" href="/">{props.nameOne}</a>
                        </li>
                        {/* <li className="nav-item">
                            <Link className="nav-link" to="/about">About Us</Link>
                        </li> */}


                    </ul>
                    {/* <form className="d-flex">
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button className="btn btn-outline-primary" type="submit">Search</button>
                    </form> */}
                    <div className="form-check form-switch">
                        <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" onClick={props.handlemessage} />
                        <label className={`form-check-label ${props.color === 'light' ? 'text-dark' : 'text-light'}`} htmlFor="flexSwitchCheckDefault "  >{props.message}</label>
                    </div>
                </div>
            </div>

        </nav>
    )
}

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    nameOne: PropTypes.string.isRequired
}

Navbar.defaultProps = {
    title: "Santosh",
    nameOne: "Why?"
}

